.app-header {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  padding: 0 !important;
  display: flex;
  align-items: center;
  height: 80px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  position: sticky;
  top: 0;
  z-index: 100;
}

.header-content {
  width: 100%;
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
}

.header-title {
  flex: 1;
}

.header-left {
  display: flex;
  align-items: center;
  gap: 16px;
}

.logo-img {
  width: 40px;
  height: 40px;
  object-fit: contain;
  display: block;
}

.header-title h1 {
  font-size: 28px;
  font-weight: 700;
  color: #ffffff;
  margin: 0;
  padding: 0;
}

.header-title p {
  font-size: 14px;
  color: rgba(255, 255, 255, 0.8);
  margin: 4px 0 0 0;
  padding: 0;
}

.logout-btn {
  white-space: nowrap;
}

.header-right {
  display: flex;
  align-items: center;
  gap: 12px;
}

.user-name {
  color: #ffffff;
}

.ant-dropdown-link {
  color: inherit;
  background: none;
  border: none;
  cursor: pointer;
  padding: 0;
  display: flex;
  align-items: center;
  transition: opacity 0.2s;
}

.ant-dropdown-link:hover {
  opacity: 0.8;
}

@media (max-width: 768px) {
  .app-header {
    height: auto;
  }

  .header-content {
    flex-direction: column;
    align-items: flex-start;
    padding: 15px 20px;
  }

  .header-title h1 {
    font-size: 24px;
  }

  .header-title p {
    font-size: 12px;
  }

  .logout-btn {
    align-self: flex-end;
  }
}
