.main-layout {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  background-color: #f5f5f5;
}

.main-content {
  flex: 1;
  padding: 30px 0;
}

.content-container {
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 30px;
  width: 100%;
}

@media (max-width: 768px) {
  .main-content {
    padding: 20px 0;
  }

  .content-container {
    padding: 0 20px;
  }
}
