.app-footer {
  background-color: #fff;
  border-top: 1px solid #e5e7eb;
  padding: 24px 30px !important;
  text-align: center;
}

.footer-content {
  max-width: 1400px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
}

.footer-content p {
  font-size: 14px;
  color: #6b7280;
  margin: 0;
  padding: 0;
}

.footer-links {
  font-size: 14px;
}

.footer-links a {
  color: #667eea;
  text-decoration: none;
  transition: color 0.3s;
}

.footer-links a:hover {
  color: #764ba2;
  text-decoration: underline;
}

.footer-links .divider {
  margin: 0 8px;
  color: #d1d5db;
}

@media (max-width: 768px) {
  .footer-content {
    flex-direction: column;
    gap: 12px;
  }

  .footer-content p {
    font-size: 12px;
  }

  .footer-links {
    font-size: 12px;
  }
}
